const Topbar: React.FC = () => {
  // 메뉴의 hover 상태를 저장하는 상태
  const [hoveredMenuIndex, setHoveredMenuIndex] = useState<number | null>(null);

  const navigate = useNavigate();


  // 홈으로 가는 명령
  const handleLogoClick = (): void => {
    navigate("/");
  };

  return (
    <header className={styles.topbar_container}>
      <div className={styles.navbar_conainer}>
        <nav className={styles.logo_nav_container}>
          <section className={styles.logo_container}>
            <img
              onClick={handleLogoClick}
              style={{ height: "90px", width: "252px" }}
              src={logo}
              alt="울산하늘공원장례식장(주)"
            />
          </section>

          <section className={styles.main_container}>
            <ul className={styles.menu_all_container}>
              {menuItems.map((menuItem, index) => (
                <li
                  key={index}
                  className={styles.menus_container}
                  onMouseEnter={() => {setHoveredMenuIndex(index); }}
                  onMouseLeave={() => {setHoveredMenuIndex(null); }}
                >
                  <span // 장례식장 소개, 시설 현황, 이용 안내, 장례안내, 공지사항
                    className={styles.menu_title}
                  >
                    <a onClick={() => navigate(menuItem.title.url)}>
                      {" "}
                      {menuItem.title.name}
                    </a>
                  </span>

                  {menuItem.subMenus ? ( // *서브메뉴가 있어야만 서브메뉴 바 출력
                    <ul className={styles.sub_menus}>
                      {menuItem.subMenus?.map((subMenu, subIndex) => (
                        <li key={subIndex} className={styles.sub_sub_menus}>
                          {subMenu.title && (
                            <span className={styles.sub_menu_title}>
                              {subMenu.title.name}
                            </span>
                          )}
                          {/* {subMenu.title && <a href={subMenu.title.url}  className={styles.sub_menu_title}>{subMenu.title.name}</a>} */}
                          <ul className={styles.sub_menu_items}>
                            {subMenu.items?.map((item, itemIndex) => (
                              <li
                                key={itemIndex}
                                className={styles.sub_menu_item}
                              >
                                <a onClick={() => navigate(item.url)}>
                                  {item.name}
                                </a>
                              </li>
                            ))}
                          </ul>
                        </li>
                      ))}
                    </ul>
                  ) : null}
                  {/* todo : 여기에 코드 넣어야함  */}

                </li>
              ))}
            </ul>
          </section>
        </nav>

        <section className={styles.add_menu_container}>
          <img src={menu} alt="울산하늘공원장례식장(주)" />
        </section>
      </div>
    </header>
  );
};

export default Topbar;
