import img1 from "./imgs/1.png";
import img2 from "./imgs/2.png";
import img3 from "./imgs/3.png";
import img4 from "./imgs/4.png";
import styles from "../Facilities.module.scss";

const Page = () => {
  return (
    <table className={styles.images}>
      <tbody>
        <tr>
          <td>
            <img src={img1} />
          </td>
          <td>
            <img src={img2} />
          </td>
        </tr>
        <tr>
          <td>
            <img src={img3} />
          </td>
          <td>
            <img src={img4} />
          </td>
        </tr>
      </tbody>
    </table>
  );
};

export default Page;
