import "./mainImage.scss";
import React from "react";

export default function MainImageBtn() {
  return (
    <div className="imageWrapper">
      <div className="contentWrapper2">
        <div className="messageWrapper">
          <div>“삼가 고인의 명복을 빕니다.”</div>
          <div>한 분 한 분 정성을 다해 모시겠습니다.</div>
        </div>
        <div className="btnWrapper">
          <div
            className="first"
            onClick={() => (window.location.href = "tel:0522549702")}
          >
            <div className="iconWrapper"></div>
            <div className="stringWrapper">
              <div className="comment">
                장례상담 및 장례식장 이용 문의 <br></br> (24시간) 전화
              </div>
              <div className="numberWrapper">
                <div className="numberIcon"></div>
                <div className="number">052) 254-9702</div>
              </div>
            </div>
          </div>
          <div
            className="two"
            onClick={() => (window.location.href = "tel:0522641017")}
          >
            <div className="iconWrapper2"></div>
            <div className="stringWrapper">
              <div className="comment">
                제사(49상, 기제사) 예약 및 화환<br></br>
                주문 전화 (07:00~16:00)
              </div>
              <div className="numberWrapper">
                <div className="numberIcon"></div>
                <div className="number">052) 264-1017</div>
              </div>
            </div>
          </div>
          <div
            className="three"
            onClick={() => (window.location.href = "tel:0521234567")}
          >
            <div className="iconWrapper3"></div>
            <div className="stringWrapper">
              <div className="comment">
                봉안함, 위패 상담 전화<br></br>
                (07:00~16:00)
              </div>
              <div className="numberWrapper">
                <div className="numberIcon"></div>
                <div className="number">010-9774-3303</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
